:root {
--text: "Orbitron";
--mainColor: #ffffff;
--minWidth: 320;
--maxWidth: 1920;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  touch-action: none !important;
}

#root {
  background: linear-gradient(180deg, #0b1f46 0%, #000 100%);
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.show-blur {
  filter: blur(1rem);
}

.h100 {
  height: 100vh!important;
}

.margin0 {
  margin: 0!important;
}



@font-face {
  font-family: "Orbitron";
  font-display: swap;
  src: local("Orbitron"),
    url("./fonts/Orbitron-Black.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
}
